</template>

<script>
import Swal from "sweetalert2";
import axios from "axios";
import moment from "moment";
import { useRoute } from "vue-router";

export default {
  name: "CusBuktiTransaksiView",
  props: ["ipAdress", "transaksi"],
  data() {
    return {
      user: JSON.parse(localStorage.getItem("SessionUser")),
      transaksiBuktiId: null,
      dataBuktiTransaksi: {},
      dataBuktiTransaksiDB: {},
    };
  },
  methods: {
    format_date(value) {
      if (value) {
        return moment(String(value)).format("DD MMMM YYYY HH:mm");
      }
    },
    getBukstiTransaksi() {
      axios({
        method: "get",
        url:
          this.ipAdress +
          "api/get-bukti-trx-cus/" +
          this.transaksiBuktiId +
          "?username=" +
          this.user.username,
      })
        .then((response) => {
          //handle success
          this.dataBuktiTransaksi = response.data.data;
          //   console.log(this.dataBuktiTransaksi);
        })
        .catch((response) => {
          //handle error
          console.log(response);
        })
        .finally(() => (this.loader = false));
    },
    getBukstiTransaksiByTrxApi() {
      axios({
        method: "get",
        url:
          this.ipAdress +
          "api/get-data-trx-cus-trxapi?username=" +
          this.user.username +
          "&trxid_api=" +
          this.transaksiBuktiId,
      })
        .then((response) => {
          //handle success
          this.dataBuktiTransaksiDB = response.data.mutasi[0];
          //   console.log(this.dataBuktiTransaksi);
        })
        .catch((response) => {
          //handle error
          console.log(response);
        })
        .finally(() => (this.loader = false));
    },
  },
  computed: {},
  mounted() {
    const route = useRoute();
    this.transaksiBuktiId = route.params.id;
    this.getBukstiTransaksi();
    this.getBukstiTransaksiByTrxApi();
  },
};
</script>

